.projectCard
  
  body {
    background: #f4f8fb;
    font-family: system-ui;
    box-sizing: border-box;
  }
  .projectCard1 {
    padding-top: auto;
    color: #263849;
  }
  a {
    text-decoration: none;
  }
  
  .projectsContainer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
    grid-gap: 2em;
    padding: 50px;
  
    .card {
      background: #ffffff;
      border-radius: 5px;
      box-shadow: 8px 14px 38px rgba(39, 44, 49, 0.06),a
        1px 3px 8px rgba(39, 44, 49, 0.03);
      transition: all 2s ease;
  
      &:hover {
        box-shadow: 0 0 1px rgba(39, 44, 49, 0.1),
          0 3px 16px rgba(39, 44, 49, 0.07);
        transition: all 0.3s ease;
        transform: translate3D(0, -1px, 0);
      }
  
      .content {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        height: 100%;
      
        padding: 20px;
        a {
          display: block;
          color: #263849;
          header {
            span {
              display: block;
              margin-bottom: 4px;
              color: #081a64;
              font-size: 12px;
              line-height: 1.15em;
              font-weight: 500;
              letter-spacing: 0.5px;
              text-transform: uppercase;
            }
            h2 {
              margin: 0 0 0.5em;
              font-size: 20px;
            }
          }
        }
      }
    }
  }
  
.author
{
  color: #081a64;
margin-top: 2rem;
margin-bottom: 0rem;
}

.author2{
  color: #081a64;
  margin-top: 5rem;
}

.author3{
  color: #081a64;
  margin-top: 6rem;
}
@media (max-width: 768px) {
  .projectsContainer {
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr)); /* Keep card size same for smaller screens */
    padding: 50px; /* Maintain padding for smaller screens */
    grid-gap: 2em; /* Maintain grid gap for smaller screens */
  }

  .projectHeader {
    font-size: 2.5rem; /* Reduce font size for smaller screens */
    padding-left: .5rem; /* Adjust padding for smaller screens */
  }

  .card .content {
    padding: 1rem; /* Maintain padding for smaller screens */
    a header h2 {
      font-size: 20px; /* Keep font size same for smaller screens */
    }
  }
}
      
    
  

